.image-box {
    height: 100px;
    &.badoo {
        position: absolute;
        left: 20%;
        top: 20%;
        transform: rotate(-45deg);
    }
}

.timeline-item {
    opacity: 0;
    animation: fadeInUp 0.5s ease-out forwards;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
